// 引入下列插件（API）
import Vue from 'vue'
import Vuex from 'vuex'
// 引入store文件中的js文件
import mutations from './mutations.js'
import actions from './actions.js'
// 全局注册
Vue.use(Vuex)
// 创建Store对象
const store = new Vuex.Store({
  // 状态数据
  state: {
    // 主题分类数据
    topics: [
          // {name: 'all', desc: '全部'},
          // {name: 'good', desc: '精华', selected: false},
          // {name: 'share', desc: '分享', selected: false},
          // {name: 'ask', desc: '问答', selected: false},
          // {name: 'job', desc: '招聘', selected: false},
          // {name: 'dev', desc: '测试', selected: false}
          {name: 'all', desc: '全部'},
          {name: 'good', desc: '精华'},
          {name: 'share', desc: '分享'},
          {name: 'ask', desc: '问答'},
          {name: 'job', desc: '招聘'},
          {name: 'dev', desc: '测试'}
    ],
    // 未登录时更多菜单
    unLoginedmoreMenus: {
      login: '登录'
    },
    // 登录时的更多菜单
    loginedMoreMenus: {
      create: '发表新主题',
      center: '个人中心',
      msg: '消息通知',
      logout: '登出'
    },
    // 登录状态
    isLogined: false,
    // 用户名属性
    loginname: '',
    // 是够显示更多菜单
    isShowMoreMenus: false,
    // 登录用户的accesstoken
    accesstoken: '',
    // 登录用户id
    author_id: ''
  },
  // 状态数据的计算属性
  getters: {
    moreMenus: function (state) {
      if (state.isLogined) {
        // 用户处理登录状态，返回登录后更多菜单
        return state.loginedMoreMenus
      }
      return state.unLoginedmoreMenus
    }
  },
  mutations,
  actions
})
// 对外输出模块
export default store



