// 用于导出该模块
export default{
  // 定义name属性
  name: 'center',
  // 定义组件内的计算属性
  computed: {
    // 为login.vue组件提供logininname的返回值
    loginname: function () {
       // 在组件内可以直接通过$store.state.loginname获取
      return this.$store.state.loginname
    }
  },
  // 创建一个created生命周期钩子函数，就是一个vue实例被生成后调用这个函数。在vue实例创建之后还要进行编译，每一个阶段都会有一个钩子函数，主要为了初始化所需的数据。
  created: function () {
    // 利用$routerreplace方法是替换当前的页面，router.replace不会向 history 添加新记录，而是替换掉当前的 history 记录。
    this.$router.replace('/user/' + this.loginname)
  }
}



