// 为了修改sotre上的数据所以必须
// 在 store 上注册 mutation，处理函数总是接受 state 作为第一个参数（如果定义在模块中，则为模块的局部状态），payload 作为第二个参数（可选）
// 注册mutation
const mutations = {
  // 为App.vue提供isShowMoreMenus
  mutationsIsShowMoreMenus: function (state, payload) {
    // state 表示当前状态数据
    // payload表示使用方法传递的附加数据
    state.isShowMoreMenus = payload
  },
  // 实现用户登录操作
  // 与login相关联，等待登录页面的调用并且修改state登录状态的值
  mutationLogin: function (state) {
    state.isLogined = true
  },
  // 修改loginname
  // 与login相关联，等待登录页面的调用并且修改loginname和登录状态
  mutationLoginname: function (state, payload) {
    state.loginname = payload
  },
  // 修改accesstoken
  // 与login相关联， 等待登录页面的调用并且修改accesstoken
  mutationAccessToken: function (state, payload) {
    state.accesstoken = payload
  },
  // 用户注销操作
  // 与logout相关联，等待被页面注销操作所调用，并且将数据恢复至初始状态
  mutationLogout: function (state) {
    state.isLogined = false
    state.loginname = ''
    state.accesstoken = ''
  },
  // 修改author_id
  // 与login相关联， 等待登录页面的调用并且修改author_id
  mutationAuthorId: function (state, payload) {
    state.author_id = payload
  }
}
export default mutations



